//FOOTER


footer{
  display: flex;
  flex-direction: column;
  color: #dddddd;
  margin-top: 25px;
  height: 513px;
  background-color: #434243;
  #list-of-themes{
    align-self: center;
    height: 354px;
    width: 996px;
    display: flex;
    justify-content: space-around;
    font-size: 14px;
    a{
      &:hover{
        color: #FFCC00;
      }
    }
  }
  ul{
    list-style: none;
    height: 252px;
    padding: 0;
    margin: 0;
    line-height: 1.7;
    li{
      border-bottom: 1px solid rgba(191, 191, 191, 0.23);
      a{
        text-decoration: none;
        color: #dddddd;
      }
    }
  }
  h3{
    font-size: 20px;
    font-weight: 300;
  }
}
#banking{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  font-size: 20px;
  height: 96px;
  background-color: #303133;
  img{
    margin-left: 5px;
  }
}
#bottom{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  font-size: 16px;
  border-top: 1px solid rgba(153, 153, 153, 0.24);
  height: 62px;
  background-color: #303133;
}
