@import "normalize.css";
@font-face {
  font-family: SegoeWP;
  src: url("../fonts/Segoe WP Regular.ttf");
}
$cw:996px;
$ff:SegoeWP;

body{
  display: flex;
    .main-wrapper{
      margin: auto;
      flex-basis: 1280px;
      font-family: $ff;
      border: solid 1px rgba(0, 0, 1, 0.16);
    }
}

@import "header";
@import "slide-bar";

#main-content-wrapper{
  margin-top: 14px;
  width: 1280px;
  display: flex;
  justify-content: center;
    .product-list-content{
     width: $cw;
      display: flex;
      justify-content: center;
        aside{
          width: 188px;
          height: 864px;
          border: solid 1px rgba(0, 0, 1, 0.16);
          margin-right: 9px;
          .categories__content{
            div{
              font-size: 24px;
              font-weight: 300;
              color: #6b6969;
              padding: 11px 0 7px 18px;
            }
          }
            button{
              display: flex;
              justify-content: start;
              align-items: center;
              width: 190px;
              height: 29px;
              background-image: linear-gradient(to top, #79b260, #92d774);
              border: none;
              color: white;
              padding-left: 21px;
              font-weight: bold;
            }
            li{
              list-style: none;
                a{
                  text-decoration: none;
                  color: #242424;
                  opacity: 85%;
                }
            }
        }
        section{
          width: 798px;
          height: 864px;
          display: flex;
          flex-direction: column;
          justify-content: start;
          align-items: start;
            .product-list__nav{
              display: flex;
              justify-content: center;
              align-items: center;
              height: 39px;
              ul{
                display: flex;
                padding: 0;
                li{
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  list-style: none;
                  width: 140px;
                  height: 37px;
                  border: solid 1px #d8dce0;
                  border-left: none;
                  background-color: #f7f7f8;
                  text-shadow: 0.5px 0.9px 0 #fff;
                  font-size: 17px;
                  font-weight: 300;
                  color: #6b6969;
                  &:first-child{
                    font-weight: bold;
                    border-bottom: none;
                    border-left: solid 1px #d8dce0;
                    background-color: white;
                  }
                  a{
                    padding: 1px;
                    text-decoration: none;
                    color: black;
                    opacity: 75%;
                  }
                }
              }
            }
            .product-list__best-sellers{
              display: flex;
              flex-wrap: wrap;
              justify-content: space-around;
              align-items: center;
              padding: 29px 21px 0 29px ;
              border: solid 1px #d8dce0;
              height: 825px;
              div{
                font-size: 15px;
                font-weight: 300;
                text-align: center;
                color: #646464;
                height: 256px;
                flex-shrink: 1;
                display: flex;
                flex-direction: column;
                flex-basis: 98px;
                margin-left: 25px;
              }
              span{
                display: block;
                font-family: HelveticaLTStd-Roman;
                font-size: 22px;
                color: #f05356;
              }
              img{
                height: 150px;
              }
            }
        }
    }
}

@import "footer";

